import { object, string } from "prop-types";
import Helmet from "react-helmet";
import React from "react";

import { favicon } from "assets/icons";
import cover from "./assets/cover.png";

const SEOWrapper = (props) => {
  const { location, title } = props;
  return (
    <Helmet
      encodeSpecialCharacters
      meta={[
        { name: "author", content: "Mogli Studio — https://moglistudio.com" },
        { name: "charset", content: "utf-8" },
        { name: "coverage", content: "Worldwide" },
        {
          name: "description",
          content:
            "Traffic Design: Biennale Sztuki Miejskiej 2018 — MODERNIZACJE"
        },
        { name: "designer", content: "Mogli Studio — https://moglistudio.com" },
        { name: "distribution", content: "Global" },
        {
          name: "keywords",
          content: "biennale, traffic, design, gdynia, festiwal"
        },
        { name: "language", content: "pl" },
        { name: "owner", content: "Traffic Design — https://trafficdesign.pl" },
        { name: "rating", content: "General" },
        { name: "revist-after", content: "after 3 days" },
        { name: "robots", content: "index, follow" },
        { name: "title", content: `Biennale Traffic Design 2018` },
        {
          name: "viewport",
          content:
            "width=device-width, minimum-scale = 1.0, initial-scale = 1.0, maximum-scale = 5.0, user-scalable=yes, shrink-to-fit=no"
        }
      ]}
      defaultTitle="Biennale Traffic Design 2018"
      titleTemplate="%s ⋅ Biennale Traffic Design 2018"
    >
      <html lang="pl" />

      <title>{title}</title>

      {/* Twitter Card */}
      <meta name="twitter:card" content="summary" />
      <meta name="twitter:creator" content="@moglistudio" />

      {/* Facebook OG */}
      <meta
        property="og:description"
        content="Traffic Design: Biennale Sztuki Miejskiej 2018 — MODERNIZACJE"
      />
      <meta property="og:image" content={cover} />
      <meta property="og:locale" content="pl" />
      <meta property="og:site_name" content="Traffic Design" />
      <meta property="og:title" content="Biennale Traffic Design 2018" />
      <meta property="og:type" content="website" />
      <meta property="og:url" content="https://trafficdesign.pl" />

      <link rel="icon" type="image/x-icon" href={favicon} />
      <link
        rel="canonical"
        href={`https://trafficdesign.pl${location.pathname}`}
      />
    </Helmet>
  );
};

SEOWrapper.propTypes = {
  location: object.isRequired,
  title: string.isRequired
};

SEOWrapper.defaultProps = {};

export default SEOWrapper;
